import { Table , Input} from "reactstrap";
import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { getcourse , checkcourse} from "../../api/user";
const Starter = () => {
  const location = useLocation();
  const selectedTrainingPlan = location.state.selectedTrainingPlan;
  const [apiData, setApiData] = useState([]);
  const [filteredData, setFilteredData] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const actionType = location.state?.actionType;
  useEffect(() => {
    console.log("Selected Training Plan ID:", selectedTrainingPlan);
    async function callApi() {
      try {
        const apiResult = await getcourse();
        console.log(apiResult);
        setApiData(apiResult);
        setFilteredData(apiResult);
      } catch (error) {
        console.log(error);
      }
    }
    callApi();
  }, []);

  const handleCheckboxChange = (id) => {
    const updatedData = apiData.map((blg) => {
      if (blg.id === id) {
        console.log("store successfully");
        blg.isChecked = !blg.isChecked;
        localStorage.setItem(`checkbox_${id}`, blg.isChecked ? "true" : "false");
      }
      return blg;
    });

    setApiData(updatedData);
  };

  const navigate = useNavigate();

  const addCheckedCourses = async () => {
    const checkedCourses = apiData.filter((blg) => blg.isChecked);
    const requests = checkedCourses.map((course) =>  
  checkcourse(selectedTrainingPlan , course.id)
    );

    try {
      await Promise.all(requests);
      console.log("Checked courses added successfully!");
      if (location.state && location.state.actionType === "edit") {
        navigate("../admintrainingplan", { state: { actionType: "edit" , history : selectedTrainingPlan} });
      } else {
        navigate("../admintrainingplan", { state: { actionType: "add" } });
      }
  
      // Clear localStorage or perform any other necessary actions after adding the courses
    } catch (error) {
      console.log("Error adding checked courses:", error);
    }
  };

  const handleSearch = (query) => {
    setSearchQuery(query);

    const filteredCourses = apiData.filter((course) =>
      course.title.toLowerCase().includes(query.toLowerCase())
    );

    setFilteredData(filteredCourses);
  };
  const handleCancel = () => {
    if (actionType === "edit") {
      navigate("../admintrainingplan", { state: { actionType: "edit" , history : selectedTrainingPlan} });
    } else {
      navigate("../admintrainingplan", { state: { actionType: "add" } });
    }
  };
  return (
    <>
      <h4 className="mb-3 caliber-font">Add Training Plan Courses</h4>
      <div className=" search-bar caliber-font ">
      <Input  style={{marginLeft:"5px" , }}
                    type="text"
                    name="search"
                    id="search"
                    placeholder="Search by name"
                    value={searchQuery}
                    onChange={(e) => handleSearch(e.target.value)}
                  />
        </div>
      <Table bordered hover className="caliber-font">
        <thead>
          <tr>
            <th>#</th>
            <th>Course</th>
            <th>Manage</th>
          </tr>
        </thead>
        <tbody>
          {filteredData.map((blg, i) => (
            <tr key={i}>
              <th scope="row">{blg.id}</th>
              <td>{blg.title}</td>
              <td>
                <input
                  type="checkbox"
                  checked={blg.isChecked || false}
                  onChange={() => handleCheckboxChange(blg.id)}
                ></input>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
      <div className="button-container">
      <button onClick={addCheckedCourses} type="submit" className="submit-button">
        Save
      </button>
      <button onClick={handleCancel} type="button" className="cancel-button">
        Cancel
      </button>
      </div>
    </>
  );
};

export default Starter;
